export default {
  name: 'Notifications',
  middleware: 'auth',
  data() {
    return {
      loading: false
    }
  },
  head: {
    title: 'Notifikasi'
  },
  methods: {
    readAll() {
      this.loading = true
      this.$axios
        .$get('account/notifications/read')
        .then(() => {
          this.$store.commit('notification/readAll')
          this.loading = false
        })
        .catch((err) => {
          this.$toast.danger(this.$errorMessage(err))
          this.loading = false
        })
    },
    deleteAll() {
      this.$toast.confirm.danger(
        () => {
          this.loading = true
          this.$axios
            .$delete('account/notifications')
            .then(() => {
              this.$store.commit('notification/set', [])
              this.loading = false
            })
            .catch((err) => {
              this.$toast.danger(this.$errorMessage(err))
              this.loading = false
            })
        },
        null,
        { message: 'Anda yakin ingin menghapus semua notifikasi?' }
      )
    },
  }
}
